html, body {
  height: 100%;
  margin: 0;
  padding: 0;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.gemini h1 {
  text-align: center;
}
.gemini-wrap {
  display: flex;
  align-items: center;
  justify-content: center;
}
.gemini button {
  height: 40px;
  background-color: #478cd6;
  padding: 10px 15px;
  border-radius: 5px;
  margin: 0 0 0 15px;
  cursor: pointer;
  color: white;
  box-shadow: none;
  border: none;
}
.gemini input {
  height: 40px;
  padding: 0;
  margin: 0;
  border: 1px solid #478cd6;
  box-shadow: none;
  box-sizing: border-box;
  border-radius: 5px;
  min-width: 200px;
  padding: 0 10px;
  outline: none;
}
.gemini .gemini-response {
  max-width: 500px;
  font-size: 14px;
}

